  font-weight: 500;
  font-style: normal;
  position: fixed;
  z-index: 2;
  overflow: hidden;
  background-color: #ffffff;
}
@media (max-width: 1023.99px) {
  .page-header {
    height: 100px;
    transition: height 1s ease 0s;
  }
}

.page-header__wrapper {
  padding: 10px 7%;
